import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Graph } from '@vx/network';


export default class Sort extends Component {
    constructor() {
        super();
        this.state = {
            width: 0, height: 0,
            size: 100,
            nodes: [], links: [], adjList: [],
        }
        this.handleClickReset = this.handleClickReset.bind(this);
        this.minimumSpanningTree = this.minimumSpanningTree.bind(this);
        this.travellingSalesmanProblem = this.travellingSalesmanProblem.bind(this);
    }

    componentDidMount() {
        const { size } = this.state;
        const width = window.innerWidth * 0.8, height = window.innerHeight * 0.8;
        const nodes = this.generateArray(size, width, height);
        this.setState({
            width, height, nodes
        });
    }

    generateArray(n, width, height) {
        var arr = [];
        for (var i = 0; i < n; i ++) { 
            arr[i] = { 
                x: 15 + Math.floor(Math.random() * (width - 30)), 
                y: 15 + Math.floor(Math.random() * (height - 30))
            } 
        }
        return arr;
    }

    handleClickReset() {
        const { size, width, height } = this.state;
        const nodes = this.generateArray(size, width, height);
        const links = [], adjList = [];
        this.setState({ nodes, links, adjList });
    }

    // travellingSalesmanProblem() {
    //     const { nodes, links } = this.state;
    //     var currentNode = 0;
    //     // while (true) {
    //     //     var nextNode = 
    //     // }
    // }

    travellingSalesmanProblem() {
        const { nodes, links, adjList } = this.state;
        var nextNodeNumber = 0, path = [];
        path.push(nextNodeNumber);

        var isRoot = false;
        var nextNode = adjList[nextNodeNumber];
        while (!isRoot) {
            while (nextNode.length) {
                console.log(nextNode);
                nextNodeNumber = nextNode.pop();
                path.push(nextNodeNumber);

                nextNode = adjList[nextNodeNumber];
            }

            console.log(path);
            console.log(links);
            isRoot = true;
        }

        // this.setState({ nodes, links, adjList });
    }

    minimumSpanningTree() {
        const { nodes, links, adjList } = this.state;
        var distances = []
        for (var i = 0; i < nodes.length - 1; i ++) {
            for (var j = i + 1; j < nodes.length; j ++) {
                var distance = Math.sqrt(Math.pow(nodes[i].x - nodes[j].x, 2) + Math.pow(nodes[i].y - nodes[j].y, 2));
                distances.push({
                    source: nodes[i], target: nodes[j], distance, i, j
                });
            }
            adjList.push([]);
        }
        adjList.push([]);

        i = 0; j = 0;
        var parent = [], rank = []
        
        distances.sort((i, j) => i.distance - j.distance);

        for (var node = 0; node < nodes.length; node ++) {
            parent.push(node);
            rank.push(0);
        }

        while (j < nodes.length - 1) {
            const link = distances[i];
            i ++;
            var x = this.find(parent, link.i);
            var y = this.find(parent, link.j);

            if (x !== y) {
                j ++;
                links.push(link);
                this.union(parent, rank, link.i, link.j);
                adjList[link.i].push(link.j);
                adjList[link.j].push(link.i);
            }
        }

        for (i = 0; i < adjList.length; i ++) { adjList[i].sort() }

        this.setState({ nodes, links, adjList })
    }

    find(parent, i) {
        if (parent[i] === i) { return i; }
        return this.find(parent, parent[i]);
    }

    union(parent, rank, x, y) {
        var xroot = this.find(parent, x);
        var yroot = this.find(parent, y);

        if (rank[xroot] < rank[yroot]) {
            parent[xroot] = yroot;
        } else if (rank[xroot] > rank[yroot]) {
            parent[yroot] = xroot;
        } else {
            parent[yroot] = xroot;
            rank[xroot] ++;
        }
    }

    render() { 
        const { width, height, nodes, links } = this.state;

        const graph = {
            nodes,
            links
        };

        return (
            <Card>
                <Card.Header>
                    <h1>TRAVELLING SALESMAN PROBLEM</h1>
                </Card.Header>
                <Card.Body style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <svg width={width} height={height}>
                        <rect width={width} height={height} rx={14} fill="#272b4d" />
                        <Graph graph={graph} />
                    </svg>
                </Card.Body>
                <Card.Footer>
                    <Row>
                        <Col>
                            <Button variant="dark" onClick={this.handleClickReset}>Reset</Button>{' '}
                            <Button variant="dark" onClick={this.minimumSpanningTree}>MST</Button>{' '}
                            <Button variant="dark" onClick={this.travellingSalesmanProblem}>TSP</Button>{' '}
                        </Col>
                    </Row>
                </Card.Footer>
            </Card>
        );
    }
}